import React from 'react';
import { StaticQuery, graphql } from 'gatsby';
import Helmet from 'react-helmet';
// import favicon from '../images/avatar.jpeg';

export default () => (
	<StaticQuery
		query={graphql`
			query helmetQuery {
				site {
					siteMetadata {
						title
						author
						imageUrl
						description
						keywords
						siteUrl
						twitterUsername
					}
				}
			}
		`}
		render={data => (
			<Helmet>
				<html lang="en" />
				<meta
					name="viewport"
					content="width=device-width, initial-scale=1, maximum-scale=1, user-scalable=0"
				/>
				<meta name="description" content={data.site.siteMetadata.description} />
				<meta name="keywords" content={data.site.siteMetadata.keywords} />
				{/* Google / Search Engine Meta Tags */}
				<meta itemprop="name" content={data.site.siteMetadata.author} />
				<meta
					itemprop="description"
					content={data.site.siteMetadata.description}
				/>
				<meta itemprop="image" content={data.site.siteMetadata.imageUrl} />
				{/* Facebook Meta Tags */}
				<meta property="og:url" content={data.site.siteMetadata.siteUrl} />
				<meta property="og:type" content="website" />
				<meta property="og:title" content={data.site.siteMetadata.title} />
				<meta
					property="og:description"
					content={data.site.siteMetadata.description}
				/>
				<meta property="og:image" content={data.site.siteMetadata.imageUrl} />
				{/* Twitter Meta Tags */}
				<meta name="twitter:card" content="summary_large_image" />
				<meta name="twitter:site" content={data.site.siteMetadata.siteUrl} />
				<meta name="twitter:title" content={data.site.siteMetadata.title} />
				<meta
					name="twitter:creator"
					content={data.site.siteMetadata.twitterUsername}
				/>
				<meta
					name="twitter:description"
					content={data.site.siteMetadata.description}
				/>
				<meta name="twitter:image" content={data.site.siteMetadata.imageUrl} />
				{/* <link rel="icon" type="image/jpeg" href={favicon} sizes="32x32" /> */}

				{/* Site Title */}
				<title>{data.site.siteMetadata.title}</title>
			</Helmet>
		)}
	/>
);
