import React from 'react';
import {
	FaTwitter,
	FaGithub,
	FaMedium,
	FaInstagram,
	FaProductHunt,
	FaEnvelope,
	FaNewspaper
} from 'react-icons/fa';
import { StaticQuery, graphql } from 'gatsby';

import './style.scss';
import Emoji from './emoji';
import SocialButton from './socialButton';

const Footer = () => (
	<StaticQuery
		query={graphql`
			query SocialQuery {
				site {
					siteMetadata {
						twitter
						medium
						github
						instagram
						productHunt
						newsletter
						paypal
					}
				}
			}
		`}
		render={data => (
			<footer className="footer center has-background-light">
				<div className="content has-text-centered">
					<p>
						This website was handcrafted with plenty cups of{' '}
						<Emoji emoji="☕" />
						&ensp; and with power of GatsbyJS + Bulma CSS.
					</p>
					<p>
						Aman Mittal (@amahimself) 2018{' '}
						<img srcSet="https://licensebuttons.net/l/by-nc-nd/4.0/88x31.png" alt="license" />
					</p>
					<p className="is-size-5">
						If you'd like to collaborate on a project, app and work together
						<a href={data.site.siteMetadata.email}> please drop me an email.</a>
					</p>
					<p>
						<SocialButton href={data.site.siteMetadata.twitter}>
							<FaTwitter color="#1da1f2" size="fa-2x" />
						</SocialButton>
						&ensp;
						<SocialButton href={data.site.siteMetadata.medium}>
							<FaMedium color="#00ab6c" size="fa-2x" />
						</SocialButton>
						&ensp;
						<SocialButton href={data.site.siteMetadata.github}>
							<FaGithub color="black" size="fa-2x" />
						</SocialButton>
						&ensp;
						<SocialButton href={data.site.siteMetadata.instagram}>
							<FaInstagram color="#e1306c" size="fa-2x" />
						</SocialButton>
						&ensp;
						<SocialButton href={data.site.siteMetadata.email}>
							<FaEnvelope color="#ffd700" size="fa-2x" />
						</SocialButton>
						&ensp;
						<SocialButton href={data.site.siteMetadata.productHunt}>
							<FaProductHunt color="#da552f" size="fa-2x" />
						</SocialButton>
						&ensp;
						<SocialButton href={data.site.siteMetadata.newsletter}>
							<FaNewspaper color="#00c1b2" size="fa-2x" />
						</SocialButton>
					</p>

					<p className="is-size-6">
						P.S. If you’re feeling really generous, and would like to help me
						continue writing tutorials, you can buy me a coffee by sending $2 to
						my{' '}
						<a
							href={data.site.siteMetadata.paypal}
							target="_blank"
							rel="noopener noreferrer"
						>
							{' '}
							PayPal
						</a>
						.
					</p>
				</div>
			</footer>
		)}
	/>
);

export default Footer;
