import React from 'react';
import { StaticQuery, graphql } from 'gatsby';

import './style.scss';
import { OutboundLink } from 'gatsby-plugin-google-analytics';

const ProjectLink = props => (
	<OutboundLink href={props.href} target="_blank" rel="noopener noreferrer">
		{props.children}
	</OutboundLink>
);

const Projects = () => (
	<StaticQuery
		query={graphql`
			query projectsQuery {
				allProjectsJson {
					edges {
						node {
							id
							title
							description
							image
							tag
							link
						}
					}
				}
			}
		`}
		render={data => (
			<div>
				<section className="section gradientBg site-projects">
					<div className="container center">
						<h1 className="title has-text-white">Projects I've Worked On</h1>
					</div>
				</section>
				<section className="section gradientBg">
					<div className="container center">
						<div className="columns is-multiline">
							{data.allProjectsJson.edges.map(({ node }) => (
								<div className="column is-one-fifth" key={node.id}>
									<div className="card">
										<div className="card-image">
											<figure className="image is-125x125">
												<img srcSet={node.image} alt="ss2" />
											</figure>
										</div>
										<div className="card-content">
											<div className="content">
												<ProjectLink href={node.link}>{node.title}</ProjectLink>
												<br />
												<p>{node.description}</p>
												<br />
												<small>
													Built with <span className="tag">{node.tag}</span>
												</small>
											</div>
										</div>
									</div>
								</div>
							))}
						</div>
					</div>
				</section>
			</div>
		)}
	/>
);

export default Projects;
