import React from 'react';
import { Posts } from '../data/Posts';

import './style.scss';
import { OutboundLink } from 'gatsby-plugin-google-analytics';

const ProjectLink = props => (
	<OutboundLink href={props.href} target="_blank" rel="noopener noreferrer">
		{props.children}
	</OutboundLink>
);

const BlogPosts = () => (
	<div>
		<section className="section gradientBg site-projects">
			<div className="container center site-blog-link">
				<h1 className="title has-text-white">
					WRITER- Few Things I've Written|
					<a href="https://medium.com/@amanhimself" className="">
						Full Blog on Medium
					</a>
				</h1>
			</div>
		</section>
		<section className="section gradientBg">
			<div className="container center">
				<div className="columns is-multiline">
					{Posts.map(post => (
						<div className="column is-one-third">
							<div className="card site-posts">
								<div className="card-image">
									<figure className="image is-200x200">
										<img srcSet={post.image} alt="ss1" />
									</figure>
								</div>
								<div className="card-content">
									<div className="content">
										<ProjectLink href={post.link}>{post.title}</ProjectLink>
									</div>
								</div>
							</div>
						</div>
					))}
				</div>
			</div>
		</section>
	</div>
);
export default BlogPosts;
